import React, { Component } from 'react'
import CommentInput from './comment/CommentInput'
import CommentList from './comment/CommentList'
//import PropTypes from 'prop-types'
import axios from 'axios'

// const Comment = (props) => {

// }

class Comment extends Component {
  constructor(props) {
    super(props)
    this.state = {
      comments: [],
      username: '',
    }
  }

  componentDidMount() {
    axios
      .get('/api/comments/')
      .then((res) => {
        this.setState({ comments: res.data })
      })
      .catch((err) => {
        console.log(err)
      })
  }

  handleSubmitComment(comment) {
    if (!comment) return
    if (!comment.username) return alert('ãƒ¦ãƒ¼ã‚¶åã‚’å…¥åŠ›ã—ã¦ãã ã•ã„ï¼')
    if (!comment.content) return alert('ã‚³ãƒ¡ãƒ³ãƒˆã‚’å…¥åŠ›ã—ã¦ãã ã•ã„ï¼')
    axios
      .post('/api/comments/add', comment)
      .then(this.componentDidMount.bind(this))
      .catch((err) => {
        console.log(err)
      })
  }

  handleDeleteComment(id) {
    axios
      .delete('/api/comments/' + id)
      .then((res) => console.log(res.data))
      .catch((err) => {
        console.log(err)
      })

    this.setState({
      comments: this.state.comments.filter((el) => el._id !== id),
    })
  }

  render() {
    return (
      <div className='wrapper'>
        {
          // è¿™é‡Œçš„onSubmitå°±ç›¸å½“äº this.handleSubmitComment({username, content})
          // è¿‡ç¨‹:
          // ãƒ»button onClick={this.handleSubmit.bind(this)} è°ƒç”¨handleSubmit
          // ãƒ»handleSubmitåˆ¤æ–­onSubmitæ˜¯å¦å­˜åœ¨ å› ä¸ºå­˜åœ¨ æ‰€ä»¥onSubmit() = onSubmit(CommentInput.state({username, content}))
          // ãƒ»æ‰€ä»¥ onSubmit() = this.handleSubmitComment({username, content})
        }
        <CommentInput onSubmit={this.handleSubmitComment.bind(this)} />
        <CommentList
          comments={this.state.comments}
          onDeleteComment={this.handleDeleteComment.bind(this)}
        />
      </div>
    )
  }
}

export default Comment
